.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin 0.5s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: black;
  background-color: #ffd900;
  border-radius: 5%;
  padding: 10px 40px;
  text-decoration: none;
}


p:nth-child(2) {
  animation: fade-in 0.8s linear forwards 0.2s;
  opacity: 0;
}

p:nth-child(3) {
  animation: fade-in 0.8s linear forwards 0.4s;
  opacity: 0;
}

a {
  animation: fade-in 0.8s linear forwards 0.6s;
  opacity: 0;
}

a:hover {
  transform: scale(1.2);
  transition-duration: 0.2s;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



@keyframes App-logo-spin {
  from {
    opacity: 0;
    transform: rotate(180deg) scale(0.2); 
  }
  to {
    opacity: 1;
    transform: rotate(360deg) scale(1);
    top: 0;
    
  }
}




